<template>
    <div class="cases-blocks">
        <p class="cases-list-title">Кейсы</p>
        <router-link @click.native="updateOverflow()" to='/#cases'><div class="back-btn"><p>Вернуться назад</p></div></router-link>
        <div class="columns">
            <div class="column is-half">
                <router-link to="/cards/0" v-on:click.native="savePosition()"><div class="case"><p>case 0</p></div></router-link>
            </div>
            <div class="column is-half">
                <router-link to="/cards/1" v-on:click.native="savePosition()"><div class="case"><p>case 1</p></div></router-link>
            </div>
        </div>
        <div class="columns">
            <div class="column is-half">
                <router-link to="/cards/2" v-on:click.native="savePosition()"><div class="case"><p>case 2</p></div></router-link>
            </div>
            <div class="column is-half">
                <router-link to="/cards/3" v-on:click.native="savePosition()"><div class="case"><p>case 3</p></div></router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        methods:{
            updateOverflow(){
                $('html').css({'overflow':'hidden'});
                $('.box-span').css({'border-color':'white'});
                var menuItems = document.getElementsByClassName('left-menu-li');
                for (var i=0; i < 5; i++){
                    if (i == 3){
                        menuItems[i].classList.add('menu-active');
                    }else{
                        menuItems[i].classList.remove('menu-active');
                    }
                }
            },
        }
    }
</script>