<template lang="html">
  <header>
    <modal style="background-color: rgba(0,0,0,.9);" name="blogger-form" id="blogger-form">
        <img class="blogger-form-planet" src="../assets/form-planet-blogger.png" alt="">
        <p class="blogger-form-title">
            <span class="yellow">Инвестируй время в творчество!</span><br>
            а заботу о привлечении <span class="yellow">рекламодателей</span> оставь нам<br>
            мы - <span class="yellow">твой личный менеджер!</span>
        </p>
        <form
          @submit="checkBloggerForm"
          method="post"
        >

          <p>
            <input
              id="name"
              v-model="name"
              type="text"
              name="name"
              placeholder="ВАШЕ ИМЯ"
            >
           </p>

          <p>
            <input
              id="email"
              v-model="email"
              type="text"
              name="email"
              placeholder="Контактный EMAIL"
            >
          </p>

          <p>
            <input
              id="phoneNumber"
              v-model="phoneNumber"
              type="number"
              name="phoneNumber"
              placeholder="ТЕЛЕФОН"
            >
          </p>

          <p>
            <input
              id="link"
              v-model="link"
              type="text"
              name="link"
              placeholder="ССЫЛКА НА АККАУНТ"
            >
          </p>
          <button type="submit">Liftoff!</button>
        <p class="pin">*Для блогееров от 10 000 подписчиков</p>
        </form>

    </modal>

    <modal style="background-color: rgba(0,0,0,.9);" name="advertiser-form" id="advertiser-form">
      <img class="blogger-form-planet advertiser-planet" src="../assets/form-planet-advertiser.jpg" alt="">
        <p class="blogger-form-title">
            Космическое повышение <span class="yellow">вашей конверсии</span><br>
            <span class="yellow">взлетай с нами!</span>
        </p>
        <form
          @submit="checkAdviserForm"
          method="post"
        >

          <p>
            <input
              id="name"
              v-model="name"
              type="text"
              name="name"
              placeholder="ВАШЕ ИМЯ"
            >
          </p>

          <p>
            <input
              id="email"
              v-model="email"
              type="text"
              name="email"
              placeholder="Контактный EMAIL"
            >
          </p>

          <p>
            <input
              id="phoneNumber"
              v-model="phoneNumber"
              type="text"
              name="phoneNumber"
              placeholder="ТЕЛЕФОН"
            >
          </p>

          <p>
            <input
              id="startDate"
              v-model="startDate"
              type="text"
              name="startDate"
              placeholder="КОГДА ПЛАНИРУЕТЕ ЗАПУСК?"
            >
          </p>

          <p>
            <input
              id="budget"
              v-model="budget"
              type="text"
              name="budget"
              placeholder="БЮДЖЕТ"
            >
          </p>
          <button type="submit">Liftoff!</button>

        </form>
    </modal>
    <div class="header-bg"></div>
    <div class="header-logo"></div>
    <span class="header-subtitle">Рекламное агенство полного цикла</span>

    <transition-group name="btn-fade" mode="out-in">
        <div :key="droped" v-show="!droped" class="cta-button-header" @click="animating()"><span class="cta-button-header-text">LIFTOFF!</span></div>
        <div @click="show(0)" :key="droped" v-show="droped" class="cta-button-header cta-button-header-blogger"><span class="cta-button-header-text" @click="animate()">Я блогер</span></div>
        <div @click="show(1)" :key="droped" v-show="droped" class="cta-button-header cta-button-header-advertiser"><span class="cta-button-header-text cta-button-header-advertiser-text" @click="animate()">Я рекламодатель</span></div>
    </transition-group>
  </header>
</template>

<script>
export default {
    data(){
        return{
            droped: false
        }
    },

    methods:{
        animating(){
            this.droped = true;
        },
        show(formNum) {
            if (formNum == 0){
                this.$modal.show('blogger-form');
            }else{
                this.$modal.show('advertiser-form');
            }

        },
        hide () {
            this.$modal.hide('blogger-form');
            this.$modal.show('advertiser-form');
        }

    }
}
</script>

<style lang="css" scoped>
</style>
