<template>
  <div id="app">
    <div class="preloader" style="height: 100vh">
      <img src="https://media.giphy.com/media/JQpH25Y6TrRQwtF0KY/source.gif" alt="">
      <p class="preloader-down">liftoff loading</p>
      <!--<div class="preloader-circle"></div>-->
    </div>
    <div class="left-menu is-hidden-touch">
      <ul class="left-menu-ul">
        <li class="left-menu-li" @click="goToElement(0)">Главная</li>
        <li class="left-menu-li" @click="goToElement(1)">Услуги</li>
        <li class="left-menu-li" @click="goToElement(2)">О нас</li>
        <li class="left-menu-li" @click="goToElement(3)">Кейсы</li>
        <li class="left-menu-li" @click="goToElement(4)">Партнеры</li>
      </ul>
    </div>
    <div class="top-menu is-hidden-desktop">
      <ul class="top-menu-ul">
        <li class="top-menu-li" @click="goToElement(0)">Главная</li>
        <li class="top-menu-li" @click="goToElement(1)">Услуги</li>
        <li class="top-menu-li" @click="goToElement(2)">О нас</li>
        <li class="top-menu-li" @click="goToElement(3)">Кейсы</li>
        <li class="top-menu-li" @click="goToElement(4)">Партнеры</li>
      </ul>
    </div>

    <!--<div class="box">-->
      <!--<span class="box-span"></span>-->
      <!--<span class="box-span"></span>-->
      <!--<span class="box-span"></span>-->
    <!--</div>-->
    <div class="main">
      <div class="contslide">
        <header-comp></header-comp>
      </div>

      <span class="services-title">Погрузись во вселенную <p>liftoff adverts</p></span>

      <div class="contslide">

        <div class="container services">
          <!--<span class="services-lefttitle">Услуги</span>-->
          <div>
            <div class="services-card">
              <span class="services-subtitle">Этапы создания <p>успешной компании</p></span>
              <div class="container services-card-header">
                <!--<div class="services-card-header-left-btn" @click="changeSlide(-1)"><i class="arrow left"></i></div>-->
                <transition appear name="fading-title" mode="out-in"><span :key="index" class="services-card-header-title">{{card[index].title}}</span></transition>
                <!--<div class="services-card-header-right-btn" @click="changeSlide(1)"><i class="arrow right"></i></div>-->
              </div>
              <div class="columns service-card-body">
                <div class="column is-half">
                  <transition appear name="fading-description" mode="out-in"><span :key="index" class="service-card-body-description">{{card[index].description}}</span></transition>
                  <transition-group
                          appear
                          name="fading-tips"
                          mode="in-out"
                  >
                    <ul class="service-card-body-steps" v-for="tip in card[index].tips" :key="tip">
                      <li class="service-card-body-step" :key="tip">{{tip}}</li>
                    </ul>
                  </transition-group>
                </div>
              </div>
            </div>
            <transition appear name="slide-fade" mode="out-in">
              <div :key="index" class="service-planet" :style="{backgroundImage: 'url('+require('../assets/planet'+(index+1)+'.png')+')'}"></div>
            </transition>
          </div>

        </div>
      </div>

      <div class="contslide">
        <about-component></about-component>
      </div>

      <div class="contslide">
        <cases-component></cases-component>
        <router-view></router-view>
      </div>

      <div class="contslide">
        <div class="blank"></div>
      </div>

      <div class="contslide">
        <clients-component></clients-component>
      </div>

    </div>
  </div>
</template>

<script>
import HeaderComponent from './HeaderComponent.vue'
import AboutComponent from './AboutComponent.vue'
import CasesComponent from './CasesComponent.vue'
import ClientsComponent from './ClientsComponent.vue'

export default {
  name: 'app',
  components: {
    'header-comp' : HeaderComponent,
    'about-component' : AboutComponent,
    'cases-component': CasesComponent,
    'clients-component' : ClientsComponent
  },
  data(){
    return {
        publicPath: process.env.BASE_URL,
        k: 0,
        delay: false,
        pageIndex: 0,
        wd: 0,
        index: 0,
        card: [
            {
                title: 'Marketing',
                description:'Мы знаем, как продвинуть сайт в TOП быстрее, где найти пользователей, заинтересованных в товарах и услугах, и превратить их в клиентов. Качественно делаем продвижение бренда в соцсетях для достижения ваших бизнес-целей: увеличения осведомленности о товарах и услугах, вывода на рынок новых продуктов, формирования имиджа бренда.',
                tips:[
                    'Поисковое продвижение (SEO)',
                    'Контекстная и контекст-медийная реклама (SEM)',
                    'Создание с нуля и ведение сообществ (SMM)',
                    'Таргетированная реклама в крупных социальных сетях',
                    'Организация целевого трафика',
                    'Построение воронок продаж'
                ]
            },
            {
                title: 'Analytics',
                description:`Мы полностью вникаем в бизнес клиента, детально анализируем информацию, обсуждаем наши выводы и предложения с заказчиком.
                      На этапе аналиткии мы получаем полную картину, на основании которой  планируем дальнейшие шаги. `,
                tips:[
                    'Брифинг',
                    'Исследования',
                    'Анализ'
                ]
            },
            {
                title: 'Strategy',
                description:'Исследуем ситуацию в необходимом секторе рынка, анализируем конкурентов, целевую аудиторию и их скрытые мотивы. Формируем индивидуальный Marketing-Mix из каналов продвижения, которые в синергии друг с другом будут отвечать поставленным бизнес-целям. На данном этапе мы точно определим и разработаем наиболее эффективную стратегию донесения информации до целевой аудитории',
                tips:[
                    'Поиск скрытых мотивов',
                    'Подбор каналов влияния',
                    'План активаций',
                    'Контент-стратегия'
                ]
            },
            {
                title: 'Creativity',
                description:'Мы создаем Big Idea -  креативную концепцию (интегрированная, вирусная) коммуникации с целевой аудиторией, которая расширяет представление о бренде заказчика аккумулирует аудиторию вокруг него. Мы всегда создаем релевантный контент.',
                tips:[
                    'Креативная концепция',
                    'Создание контента',
                    'Нейминг'
                ]
            },
            {
                title: 'Ui/Ux design',
                description:`Независимо от креативности проекта, мы составляем визуальный бриф —  карту ваших желаний, на которые мы ориентируемся при создании дизайна.
                      Создаем удобную визуальную форму, которая помогает достичь бизнес-целей, а также делает образ бренда запоминающимся.`,
                tips:[
                    'Проектирование',
                    'Дизайн-концепция',
                    'Интерактив и анимация',
                    'Разработка логотипа компании'
                ]
            },
            {
                title: 'Web development',
                description:`В краткие сроки мы креативно разработаем веб-приложение различной сложности: от небольших служб и API до более сложных систем, с помощью которого соберем полную информацию и координаты вашей целевой аудитории.
                      Мы помогаем нашим клиентам на всех этапах создания продукта, шаг за шагом совместно разрабатывая и запуская IT решения.`,
                tips:[
                    'Вёрстка',
                    'Скриптинг',
                    'Программирование',
                    'Q/A Тестирование'
                ]
            },
            {
                title: 'Video production',
                description:`Помимо всего мы  имеем огромный опыт в создании рекламных роликов, что помогает донести аудитории информацию о вашем продукте.
                      Мы можем взять на себя всю работу по подготовке ролика, написанию сценария, съемке и монтажу.`,
                tips:[
                    'Промо-ролики'
                ]
            },
        ]
    }
  },
  mounted: function() {

      $('html').css({'overflow':'hidden'});
      $('.main').css({'display':'block'});
      $('.preloader').css({'display':'none'})

      $.fn.animateRotate = function(angle, duration, easing, complete) {
          var args = $.speed(duration, easing, complete);
          var step = args.step;
          return this.each(function(i, e) {
              args.step = function(now) {
                  $.style(e, 'transform', 'rotate(' + now + 'deg)');
                  if (step) return step.apply(this, arguments);
              };

              $({deg: 0}).animate({deg: angle}, args);
          });
      };

      $('body').addClass('stop-scrolling');
      var slides = document.getElementsByClassName('contslide');
      var scrollTimeout;
      var menuItems = document.getElementsByClassName('left-menu-li');
      var throttle = 500;

      menuItems[0].classList.add('menu-active');
      $(window).on('mousewheel DOMMouseScroll', {changeMenuItem: this.changeMenuItem,goToElement: this.goToElement,publicPath: this.publicPath,index: this.index, pageIndex: this.pageIndex, isSecondSlide: this.isSecondSlide, isThirdSlide: this.isThirdSlide, changePage : this.changePage}, function (e) {
          if (localStorage.pagePosition){
              e.data.pageIndex = localStorage.pagePosition;
              localStorage.removeItem('pagePosition');
          }

          var wd = e.originalEvent.wheelDelta || -e.originalEvent.detail;
          console.log('main pageIndex: ' + e.data.pageIndex);

          if (!scrollTimeout) {
              scrollTimeout = setTimeout(function () {
                 if ((e.data.pageIndex == 0)){    // 1 слайд
                     if (wd < 0){
                         e.data.changeMenuItem(1);
                         $('header').addClass('header_hidden');
                         e.data.pageIndex++; // прибавляем индекс для след. перехода
                         $('.box-span').css({'border-color':'white'});

                         $('.box').bind('click', function() {
                             e.data.changeMenuItem(2);
                             e.data.pageIndex++; // след страница
                             e.data.changePage(1000, e.data.pageIndex); // переход на 3 слайд
                             console.log('animation slide 2 start' + e.data.pageIndex);
                             $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //запускаем анимацию метеорита большого
                             $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                             setInterval(function () {
                                 $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //запускаем анимацию метеорита большого
                                 $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                                 console.log('poo');
                             }, 8000)
                             $('.box').unbind();
                         });

                         if (e.data.pageIndex > slides.length-1){ //проверка на выход из пределов стайдов
                             e.data.pageIndex = slides.length-1;
                         }
                     }else{
                         e.data.pageIndex--; // отнимаем индекс, если wd > 0
                         if (e.data.pageIndex < 0){ //проверка на выход из пределов стайдов
                             e.data.pageIndex = 0;
                         }
                     }
                     if ((e.data.pageIndex == 1)){
                         e.data.changePage(1000, e.data.pageIndex); //переход с 1 на 2 слайд
                     }else{
                         e.data.changePage(1000, e.data.pageIndex);
                     }
                 } else if (e.data.pageIndex == 1){ //перешли на 2 стайд
                     $(".first-row").css({'margin-left':'-200vw'}); //обновляем текст на 4 слайде
                     $(".second-row").css({'margin-right':'-200vw'}); //обновляем текст на 4 слайде
                     $(".third-row").css({'margin-left':'-200vw'}); //обновляем текст на 4 слайде
                     console.log('запущена функция для 2 страницы');
                     var result = e.data.isSecondSlide(e.data.pageIndex, wd); //функция перехода между сдайдами 2 блока
                     if (result == 'nextPage'){
                         e.data.changeMenuItem(2);
                         e.data.pageIndex++; // след страница
                         e.data.changePage(1000, e.data.pageIndex); // переход на 3 слайд
                         console.log('animation slide 2 start');

                     }else if (result == 'prevPage'){
                         e.data.changeMenuItem(0)
                         $('header').removeClass('header_hidden');
                         e.data.pageIndex--; // пред страницы
                         e.data.changePage(1000, e.data.pageIndex); //переход со 2 на 1 слайд
                     }


                 } else if (e.data.pageIndex == 2) { //перешли на 3 слайд
                     console.log('запущена функция для 3 страницы');
                     $(".first-row").css({'margin-left':'-200vw'}); //обновляем текст на 4 слайде
                     $(".second-row").css({'margin-right':'-200vw'}); //обновляем текст на 4 слайде
                     $(".third-row").css({'margin-left':'-200vw'}); //обновляем текст на 4 слайде
                     var resultThree = e.data.isThirdSlide(e.data.pageIndex, wd);
                     if (resultThree == 'nextPage') { //wd < 0 ?
                         e.data.changeMenuItem(3)
                         e.data.pageIndex++; // прибавляем индекс для перехода на след. слайд
                         console.log('pageIndex++ nextPage ');
                         e.data.changePage(1000, e.data.pageIndex); // ереход с 3 на 4 слайд
                         setTimeout(function () {
                             $(".first-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);
                         setTimeout(function () {
                             $(".second-row").animate({marginRight: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);
                         setTimeout(function () {
                             $(".third-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);


                     } else { //Если wd > 0, уменьшаем индекс
                         e.data.changeMenuItem(1);
                         $('.box').bind('click', function() {
                             e.data.changeMenuItem(2);
                             e.data.pageIndex++; // след страница
                             e.data.changePage(1000, e.data.pageIndex); // переход на 3 слайд
                             console.log('animation slide 2 start');
                             $('.box').unbind();
                         });
                         console.log('pageIndex-- prevPage ');
                         e.data.pageIndex--;
                         e.data.changePage(1000, e.data.pageIndex); // переход с 4 на 2 слайд
                         setTimeout(function () {
                             $(".first-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);
                         setTimeout(function () {
                             $(".second-row").animate({marginRight: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);
                         setTimeout(function () {
                             $(".third-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                         }, 100);
                     }
                 }else if (e.data.pageIndex == 3){ // 4 слайд
                     if (wd < 0){
                         e.data.changeMenuItem(4)
                         $('.box-span').css({'border':'none'});
                         e.data.pageIndex++;
                         e.data.changePage(1000, 1, e.data.pageIndex); //переход на 5 слайд
                         $(".sattelite").addClass('sattelite-animate') //запуск анимации саттелита
//                         setTimeout(function () {
//                             $(".clients-table").addClass('clients-table-animate') //проявление клиентов
//                         }, 5150);
                     }else{
                         e.data.changeMenuItem(2)
                         e.data.pageIndex--;
                         e.data.changePage(1000, e.data.pageIndex); // переход на 3 слайд

                     }
                 }
                 else if (e.data.pageIndex == 4) {
                     if (wd > 0){
                         $('.box-span').css({'border-right':'3px solid #fff'});
                         $('.box-span').css({'border-bottom':'3px solid #fff'});
                         e.data.changeMenuItem(3)
                         e.data.pageIndex--;
                         e.data.changePage(1000, e.data.pageIndex); //переход на 4 слайд
                     }
                 }

                  scrollTimeout = null;
              }, throttle);
          }

      });
  },
  beforeDestroy(){
      $(window).off('mousewheel DOMMouseScroll');
  },

  methods: {
      initParticlesJS () {
       /* eslint-disable */
       particlesJS('particles-js', {
         'particles': {
           'number': {
             'value': 60,
             'density': {
               'enable': true,
               'value_area': 800
             }
           },
           'color': {
             'value': '#ffffff'
           },
           'shape': {
             'type': 'circle',
             'stroke': {
               'width': 0,
               'color': '#000000'
             },
             'polygon': {
               'nb_sides': 5
             },
             'image': {
               'src': 'img/github.svg',
               'width': 100,
               'height': 100
             }
           },
           'opacity': {
             'value': 0.5,
             'random': false,
             'anim': {
               'enable': false,
               'speed': 1,
               'opacity_min': 0.1,
               'sync': false
             }
           },
           'size': {
             'value': 3,
             'random': true,
             'anim': {
               'enable': false,
               'speed': 20,
               'size_min': 0.1,
               'sync': false
             }
           },
           'line_linked': {
             'enable': false,
             'distance': 300,
             'color': '#ffffff',
             'opacity': 0.4,
             'width': 2
           },
           'move': {
             'enable': true,
             'speed': 1,
             'direction': 'none',
             'random': false,
             'straight': false,
             'out_mode': 'out',
             'bounce': false,
             'attract': {
               'enable': false,
               'rotateX': 600,
               'rotateY': 1200
             }
           }
         },
         'interactivity': {
           'detect_on': 'canvas',
           'events': {
             'onhover': {
               'enable': false,
               'mode': 'repulse'
             },
             'onclick': {
               'enable': false,
               'mode': 'push'
             },
             'resize': true
           },
           'modes': {
             'grab': {
               'distance': 800,
               'line_linked': {
                 'opacity': 1
               }
             },
             'bubble': {
               'distance': 800,
               'size': 80,
               'duration': 2,
               'opacity': 0.8,
               'speed': 3
             },
             'repulse': {
               'distance': 400,
               'duration': 0.4
             },
             'push': {
               'particles_nb': 4
             },
             'remove': {
               'particles_nb': 2
             }
           }
         },
         'retina_detect': true
       })
     },
      changeSlide : function (input) {
          if (((this.index + input) > -1) && ((this.index + input) < 7)) {
              this.index = this.index + input;
          }
      },

      changePage : function (time, index, inc) {
          var slides = document.getElementsByClassName('contslide');
          inc = inc || 0;
          $('html, body').animate({
              scrollTop: slides[index + inc].offsetTop,
          }, {
              duration: time, //2500
          });
      },



      changeMenuItem : function(num){
          var menuItems = document.getElementsByClassName('left-menu-li');
          for (var i=0; i < 5; i++){
              if (i == num){
                  menuItems[i].classList.add('menu-active');
              }else{
                  menuItems[i].classList.remove('menu-active');
              }
          }
      },


      goToElement : function (num) {

          if (num == 4){
              this.changePage(1000, num + 1);
              localStorage.pagePosition = num;
              this.changeMenuItem(num);
          } else{
              this.changePage(1000, num);
              localStorage.pagePosition = num;
              this.changeMenuItem(num);
              if (num == 0){
                  $('header').removeClass('header_hidden');
              }
              if (num == 2){
                  $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //запускаем анимацию метеорита большого
                  $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                  setInterval(function () {
                      $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //запускаем анимацию метеорита большого
                      $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                      console.log('poo');
                  }, 8000)
              }
              if (num == 3){
                  setTimeout(function () {
                      $(".first-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                  }, 100);
                  setTimeout(function () {
                      $(".second-row").animate({marginRight: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                  }, 100);
                  setTimeout(function () {
                      $(".third-row").animate({marginLeft: "0"}, {duration: 1500}); //сразу запускаем анимацию текста на 4 слайде
                  }, 100);
              }
          }
      },

      isSecondSlide : function (index, wd) {
          if (index == 1){

              if (wd < 0) {
                  this.k++;
                  this.changeSlide(1);
                  if (this.k == 7){
                      this.k = 6;
                      return 'nextPage';
                  }
              }
              else {
                  this.k--;
                  this.changeSlide(-1);
                  if (this.index == 0){
                      return 'prevPage';
                  }
              }
          }
      },
      isThirdSlide : function (index, wd) {
          if (index == 2){
              if (wd < 0) {
                  return 'nextPage';
              }
          }
      },
   }
}
</script>

<style>
@import "../../css/style.css";
@import "../main.css";
@import "../fonts.css";
</style>